fileDialogIsShow = true;\n    },\n    feclarationFileView() {\n      this.fileDialogIsShow = true;\n    },\n    goodsDialogOkHandler() {\n      this.goodsDialogIsShow = false;\n      this.$notify({\n        title: '成功',\n        message: '保存成功',\n        type: 'success',\n        duration: 2000,\n      });\n    },\n    addAndEditOkHandler() {\n      let validateForm = () => {\n        return new Promise((resolve, reject) => {\n          this.$refs['ptDataRef'].validate(valid => {\n            if (valid) {\n              return resolve(true);\n            }\n            return reject(false);\n          });\n        });\n      };\n\n      let addForm = () => {\n        return processingTradeAPI.addFormData(this.ptDataModel).then(data => {\n          this.loadProcessingTradeList();\n          return data;\n        });\n      };\n\n      let editForm = () => {\n        return processingTradeAPI.editFormData(this.ptDataModel).then(data => {\n          this.loadProcessingTradeList();\n          return data;\n        });\n      };\n\n      validateForm()\n        .then(() => {\n          if (this.editMode === 1) {\n            return addForm();\n          }\n          if (this.editMode === 2) {\n            return editForm();\n          }\n        })\n        .then(res => {\n          if (res.status === 1) {\n            this.addAndEditDialogIsShow = false;\n            this.$notify({\n              title: '成功',\n              message: res.message,\n              type: 'success',\n              duration: 2000,\n            });\n          } else {\n            this.$notify.error({\n              title: '失败',\n              message: res.message,\n              duration: 2000,\n            });\n          }\n        })\n        .catch(e => {\n          console.log(e);\n          this.$notify.error({\n            title: '输入错误',\n            message: '没有正确填写表单项！',\n            duration: 2000,\n          });\n        });\n    },\n    goodsAddAndEditOkHandler() {\n      this.goodsAddAndEditDialogIsShow = false;\n      this.$notify({\n        title: '成功',\n        message: '保存成功',\n        type: 'success',\n        duration: 2000,\n      });\n    },\n    fileViewOkHandler() {\n      this.fileDialogIsShow = false;\n      this.$notify({\n        title: '成功',\n        message: '保存成功',\n        type: 'success',\n        duration: 2000,\n      });\n    },\n    ptHandleSizeChange(val) {\n      this.ptPageSize = val;\n      this.loadProcessingTradeList();\n    },\n    ptHandleCurrentChange(val) {\n      this.ptCurrentPage = val;\n      this.loadProcessingTradeList();\n    },\n  },\n  created() {\n    this.loadProcessingTradeList();\n    this.loadGoodsList();\n    this.clientHeight = document.documentElement.clientHeight - 270;\n    this.clientWidth = document.documentElement.clientWidth - 250;\n    let num = Math.floor(this.clientHeight / 40) - 1;\n    this.ptPageSize = Math.floor(num / 5) * 5;\n    this.ptPageSizes = [\n      this.ptPageSize,\n      this.ptPageSize * 2,\n      this.ptPageSize * 4,\n    ];\n  },\n};\n</script>\n\n<style scoped>\n.search-bar {\n  padding-bottom: 10px;\n}\n\n.main-content-wrap {\n  padding: 10px;\n}\n</style>\n"],"sourceRoot":""}]);

